import React from "react";

import "./Profile.sass";
import bg from "./bg1.jpg";
import Post from "./Post/Post";
import ProfileInfo from "./ProfileInfo/ProfileInfo";

const Profile = () => {
  return (
    <div>
      <img src={bg} alt="cactus" className="bg" />
      <ProfileInfo />
      <Post />
    </div>
  );
};

export default Profile;
