import React from "react";
import DialogItem from "../Dialogs/DialogItem/DialogItem";
import DialogMessage from "../Dialogs/DialogMessage/DialogMessage";
import "./Dialogs.sass";

const Dialogs = (props) => {
  let dialogsData = [
    { id: 1, name: "Dimych" },
    { id: 2, name: "Andrey" },
    { id: 3, name: "Sveta" },
    { id: 4, name: "Sasha" },
    { id: 5, name: "Viktor" },
    { id: 6, name: "Valera" }
  ];
  let messageData = [
    { id: 1, message: "Hi" },
    { id: 2, message: "How is your cactus?" },
    { id: 3, message: "Yo" },
    { id: 4, message: "Hi" },
    { id: 5, message: "Hi" },
    { id: 6, message: "what is cactus?" }
  ];
  return (
    <div className="dialogs">
      <div className="dialogs-items">
        <DialogItem name={dialogsData[0].name} id={dialogsData[0].id} />
        <DialogItem name={dialogsData[1].name} id={dialogsData[1].id} />
        <DialogItem name={dialogsData[2].name} id={dialogsData[2].id} />
        <DialogItem name={dialogsData[3].name} id={dialogsData[3].id} />
        <DialogItem name={dialogsData[4].name} id={dialogsData[4].id} />
        <DialogItem name={dialogsData[5].name} id={dialogsData[5].id} />
      </div>
      <div className="messages">
        <DialogMessage message={messageData[0].message} />
        <DialogMessage message={messageData[1].message}  />
        <DialogMessage message={messageData[2].message}  />
        <DialogMessage message={messageData[3].message}  />
        <DialogMessage message={messageData[4].message}  />
        <DialogMessage message={messageData[5].message}  />
      </div>
    </div>
  );
};
export default Dialogs;
